@mixin left {
  left: 10%;
  transform: translateX(-50%) rotate(-30deg);
}
@mixin right {
  left: 90%;
  transform: translateX(-50%) rotate(30deg);
}
@mixin center {
  left: 50%;
  transform: translateX(-50%);
}
@mixin f-left {
  left: 30%;
  transform: translateX(-50%) rotate(-12deg);
}

@mixin f-right {
  left: 70%;
  transform: translateX(-50%) rotate(12deg);
}

.ball-context-menu {
  background: white;
  border-radius: 20px;
  box-shadow: $mainShadow;
  overflow: hidden;

  & li {
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    & p {
      font-size: 20px;
      padding: 5px;
      color: $darkgreen;
      transition: all 0.3s ease-in-out;
    }
    &:hover {
      background: $maingreen;
      box-shadow: inset $mainShadow;
      & p {
        color: $white;
      }
    }
  }
}
.balloon-img-menu {
  display:flex;
  justify-content: center;
  width: 100%;
  height: 50%;
  position: absolute;
  z-index: 100;
  
  left:0;
  box-shadow: $mainShadow;
  background: $lightGreen;
  /* border: 2px dotted #2bbbad; */
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  transition: all 0.3s ease;
  & .balloon-img-menu-wrapper {
    display: flex;
    flex-direction: column;
    overflow: auto;
    width:90%;
    margin-top:20px;
    & .menu-container{
      margin-top:20px;
      & .menu-item{
        & .menu-item-imgs{
          & .img-container{
            margin:0 10px;
            & img{
              transition: transform .3s ease;
              cursor:pointer;
              &:hover{
                transform:scale(1.1);
              }
            }
          }
        }
      }
    }
  }
}
